import Spirit from "../../base/Spirit";

/**
 * 动态道具类，继承精灵类
 */
class TrendsProp extends Spirit {
    constructor(name,image,left,top,width,height,xSpeed,ySpeed,imageIndex,changeDelay,currDelay,score) {
        super(name,image,left,top,width,height);
        this.xSpeed = xSpeed;
        this.ySpeed = ySpeed;
        this.imageIndex = imageIndex;
        this.changeDelay = changeDelay;
        this.currDelay = currDelay;
        this.score = score;
    }
}

export default TrendsProp
